import React, { useState, useEffect } from "react";
import { db } from './firebase';
import { doc, getDoc, setDoc } from "firebase/firestore";
import toast, { Toaster } from 'react-hot-toast';

// Icon components (No changes)
const Menu = ({ className }) => ( <svg className={className} fill="none" stroke="currentColor" viewBox="0 0 24 24"> <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 6h16M4 12h16M4 18h16" /> </svg> );
const Plus = ({ className }) => ( <svg className={className} fill="none" stroke="currentColor" viewBox="0 0 24 24"> <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 4v16m8-8H4" /> </svg> );
const Edit3 = ({ className }) => ( <svg className={className} fill="none" stroke="currentColor" viewBox="0 0 24 24"> <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M11 5H6a2 2 0 00-2 2v11a2 2 0 002 2h11a2 2 0 002-2v-5m-1.414-9.414a2 2 0 112.828 2.828L11.828 15H9v-2.828l8.586-8.586z" /> </svg> );
const Trash2 = ({ className }) => ( <svg className={className} fill="none" stroke="currentColor" viewBox="0 0 24 24"> <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16" /> </svg> );
const Eye = ({ className }) => ( <svg className={className} fill="none" stroke="currentColor" viewBox="0 0 24 24"> <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 12a3 3 0 11-6 0 3 3 0 016 0z" /> <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M2.458 12C3.732 7.943 7.523 5 12 5c4.478 0 8.268 2.943 9.542 7-1.274 4.057-5.064 7-9.542 7-4.477 0-8.268-2.943-9.542-7z" /> </svg> );
const Download = ({ className }) => ( <svg className={className} fill="none" stroke="currentColor" viewBox="0 0 24 24"> <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 16v1a3 3 0 003 3h10a3 3 0 003-3v-1m-4-4l-4 4m0 0l-4-4m4 4V4" /> </svg> );
const QrCode = ({ className }) => ( <svg className={className} fill="none" stroke="currentColor" viewBox="0 0 24 24"> <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M3 7V5a2 2 0 012-2h2m0 0V1m0 2h2m8 0h2a2 2 0 012 2v2m-4-4V1m0 2h2m0 14v2a2 2 0 01-2 2h-2m4-4v-2m-4 4h-2m-8 0H5a2 2 0 01-2-2v-2m4 4v2m-4-4h2M3 7h2v2H3V7zm16 0h2v2h-2V7zM3 17h2v2H3v-2zm16 0h2v2h-2v-2zM9 9h2v2H9V9zm4 0h2v2h-2V9zm0 4h2v2h-2v-2z" /> </svg> );
const X = ({ className }) => ( <svg className={className} fill="none" stroke="currentColor" viewBox="0 0 24 24"> <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" /> </svg> );

const QRCodeComponent = ({ value, size = 200 }) => {
  const qrCodeUrl = `https://api.qrserver.com/v1/create-qr-code/?size=${size}x${size}&data=${encodeURIComponent(value)}`;
  return ( <img src={qrCodeUrl} alt="QR Code" width={size} height={size} className="border-2 border-gray-300 rounded" /> );
};

const downloadQRCode = async (restaurantName, qrCodeUrl) => {
  try {
    const response = await fetch(qrCodeUrl);
    const blob = await response.blob();
    const link = document.createElement('a');
    link.href = URL.createObjectURL(blob);
    link.download = `${restaurantName.replace(/\s+/g, '-')}-menu-qr.png`;
    link.click();
    URL.revokeObjectURL(link.href);
  } catch (error) { console.error('Failed to download QR code:', error); window.open(qrCodeUrl, '_blank'); }
};

const generateCustomerMenuUrl = () => {
    const baseUrl = 'https://therohitbiruli.github.io/dinex-menu';
    return `${baseUrl}/#/menu`; 
};

const MenuItemForm = ({ item, onSave, onCancel, categories }) => {
    const [formData, setFormData] = useState(item || { name: "", category: categories[0] || "", price: "", description: "", imageUrl: "", popular: false, isVeg: true, chefSpecial: false });
    const [imageFile, setImageFile] = useState(null);
    const [isUploading, setIsUploading] = useState(false);
  
    const handleImageUpload = async () => {
      if (!imageFile) return formData.imageUrl;
      setIsUploading(true);
      const data = new FormData();
      data.append("file", imageFile);
      // !!! IMPORTANT: Remember to replace this with your actual Upload Preset Name !!!
      data.append("upload_preset", "dinex_uploads"); 
      try {
        const res = await fetch("https://api.cloudinary.com/v1_1/dlelfhwmi/image/upload", { method: "POST", body: data });
        const file = await res.json();
        setIsUploading(false);
        return file.secure_url;
      } catch (error) {
        setIsUploading(false); toast.error("Image upload failed."); return null;
      }
    };
  
    const handleSubmit = async (e) => {
      e.preventDefault();
      if (!formData.name || !formData.price) return;
      const finalImageUrl = await handleImageUpload();
      if (finalImageUrl === null) return;
      onSave({ ...formData, imageUrl: finalImageUrl, price: parseFloat(formData.price) || 0, });
    };
  
    return (
      <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4">
        <div className="bg-white rounded-lg shadow-xl max-w-md w-full max-h-[90vh] overflow-y-auto">
          <div className="p-6">
            <div className="flex justify-between items-center mb-4">
              <h3 className="text-lg font-semibold">{item?.id ? 'Edit Menu Item' : 'Add Menu Item'}</h3>
              <button onClick={onCancel} className="text-gray-400 hover:text-gray-600"><X className="w-5 h-5" /></button>
            </div>
            <form onSubmit={handleSubmit} className="space-y-4">
              <div><label className="block text-sm font-medium text-gray-700 mb-1">Name *</label><input type="text" value={formData.name} onChange={(e) => setFormData({...formData, name: e.target.value})} className="w-full px-3 py-2 border rounded-lg" required /></div>
              <div><label className="block text-sm font-medium text-gray-700 mb-1">Category</label><select value={formData.category} onChange={(e) => setFormData({...formData, category: e.target.value})} className="w-full px-3 py-2 border rounded-lg">{categories.map(cat => (<option key={cat} value={cat}>{cat}</option>))}</select></div>
              <div><label className="block text-sm font-medium text-gray-700 mb-1">Price (₹) *</label><input type="number" value={formData.price} onChange={(e) => setFormData({...formData, price: e.target.value})} className="w-full px-3 py-2 border rounded-lg" required /></div>
              <div><label className="block text-sm font-medium text-gray-700 mb-1">Description</label><textarea value={formData.description} onChange={(e) => setFormData({...formData, description: e.target.value})} className="w-full px-3 py-2 border rounded-lg" rows={3}/></div>
              <div><label className="block text-sm font-medium text-gray-700 mb-1">Image URL</label><input type="url" value={formData.imageUrl} onChange={(e) => setFormData({...formData, imageUrl: e.target.value})} className="w-full px-3 py-2 border rounded-lg" placeholder="https://example.com/image.jpg" /><div className="text-center text-xs text-gray-500 my-2">OR</div><label className="block text-sm font-medium text-gray-700 mb-1">Upload an Image</label><input type="file" accept="image/*" onChange={(e) => setImageFile(e.target.files[0])} className="w-full text-sm text-gray-500 file:mr-4 file:py-2 file:px-4 file:rounded-full file:border-0 file:text-sm file:font-semibold file:bg-blue-50 file:text-blue-700 hover:file:bg-blue-100" />{(formData.imageUrl || imageFile) && ( <div className="mt-2"> <p className="text-xs text-gray-600 mb-1">Preview:</p> <img src={imageFile ? URL.createObjectURL(imageFile) : formData.imageUrl} alt="Preview" className="w-24 h-24 rounded-lg object-cover" /> </div> )}</div>
              <div className="space-y-2"><div className="flex items-center"><input type="checkbox" id="isVeg" checked={formData.isVeg} onChange={(e) => setFormData({...formData, isVeg: e.target.checked})} className="mr-2" /><label htmlFor="isVeg" className="text-sm font-medium text-gray-700">Vegetarian</label></div><div className="flex items-center"><input type="checkbox" id="popular" checked={formData.popular} onChange={(e) => setFormData({...formData, popular: e.target.checked})} className="mr-2" /><label htmlFor="popular" className="text-sm font-medium text-gray-700">Popular Item</label></div><div className="flex items-center"><input type="checkbox" id="chefSpecial" checked={formData.chefSpecial} onChange={(e) => setFormData({...formData, chefSpecial: e.target.checked})} className="mr-2" /><label htmlFor="chefSpecial" className="text-sm font-medium text-gray-700">Chef's Special</label></div></div>
              <div className="flex gap-3 pt-4">
                <button type="submit" disabled={isUploading} className="flex-1 bg-blue-600 text-white px-4 py-2 rounded-lg hover:bg-blue-700 disabled:bg-blue-300">
                  {isUploading ? 'Uploading...' : (item?.id ? 'Update Item' : 'Add Item')}
                </button>
                <button type="button" onClick={onCancel} className="flex-1 bg-gray-200 text-gray-700 px-4 py-2 rounded-lg hover:bg-gray-300"> Cancel </button>
              </div>
            </form>
          </div>
        </div>
      </div>
    );
};

const ScanToSeeMenuApp = () => {
  const [currentView, setCurrentView] = useState("landing");
  const [restaurants, setRestaurants] = useState([]);
  const [selectedRestaurant, setSelectedRestaurant] = useState(null);
  const [menuItems, setMenuItems] = useState([]);
  const [editingItem, setEditingItem] = useState(null);
  const [editingRestaurant, setEditingRestaurant] = useState(false);
  const [viewingImage, setViewingImage] = useState(null);
  const [categories, setCategories] = useState([]);
  const [newCategoryInput, setNewCategoryInput] = useState("");

  useEffect(() => {
    const loadData = async () => {
      const docRef = doc(db, "menus", "the-spice-route");
      const docSnap = await getDoc(docRef);
      if (docSnap.exists()) { setMenuItems(docSnap.data().items || []); } 
      else { console.log("No menu document found in Firestore! Starting fresh."); }
    };
    loadData();
    const sampleRestaurant = { id: "rest_1", name: "The Spice Route", description: "Authentic Indian flavors with a contemporary touch." };
    const sampleCategories = ["Appetizers", "Main Courses", "Breads", "Rice", "Desserts"];
    setRestaurants([sampleRestaurant]);
    setSelectedRestaurant(sampleRestaurant);
    setCategories(sampleCategories);
  }, []);

  const addMenuItem = (newItem) => { const item = { ...newItem, id: Date.now() }; setMenuItems(prev => [...prev, item]); setEditingItem(null); };
  const updateMenuItem = (updatedItem) => { setMenuItems(prev => prev.map(item => item.id === updatedItem.id ? updatedItem : item)); setEditingItem(null); };
  const deleteMenuItem = (id) => { setMenuItems(prev => prev.filter(item => item.id !== id)); };
  const updateRestaurant = (updatedRestaurant) => { setSelectedRestaurant(updatedRestaurant); setRestaurants(prev => prev.map(r => r.id === updatedRestaurant.id ? updatedRestaurant : r)); setEditingRestaurant(false); };

  const handleSave = async () => {
    try {
      await setDoc(doc(db, "menus", "the-spice-route"), { items: menuItems });
      toast.success('Menu saved successfully!');
    } catch (e) {
      console.error("Error saving document: ", e);
      toast.error('Error saving menu.');
    }
  };

  // These variables are now declared inside the main component scope
  const customerMenuUrl = generateCustomerMenuUrl();
  const isReadyForQR = selectedRestaurant?.name?.trim() && categories.length > 0 && menuItems.length > 0;

  return (
    <div>
      <Toaster position="bottom-center" />

      {currentView === "landing" && (
        <div className="min-h-screen bg-gradient-to-br from-purple-700 via-pink-600 to-orange-500 text-white flex items-center justify-center">
          <div className="text-center px-4 py-8">
            <Menu className="w-16 h-16 mx-auto mb-4" />
            <h1 className="text-5xl font-extrabold mb-3 tracking-wide">Welcome to <span className="text-yellow-300">DineX</span></h1>
            <p className="text-lg md:text-xl mb-4 font-light">Powered by <span className="font-semibold text-white">Quintex Digital Solutions</span></p>
            <p className="text-2xl mb-8 font-medium">The Future of Restaurant Menus</p>
            <div className="flex flex-col md:flex-row gap-4 justify-center">
              <button onClick={() => setCurrentView("panel")} className="bg-yellow-400 text-purple-900 px-8 py-3 rounded-full font-bold shadow hover:scale-105 transition"> Management Panel </button>
              <button onClick={() => setCurrentView("menu")} className="bg-transparent border-2 border-white px-8 py-3 rounded-full font-bold hover:bg-white hover:text-purple-800 transition"> View Menu </button>
            </div>
          </div>
        </div>
      )}

      {currentView === "panel" && (
        <div className="min-h-screen bg-gray-50">
          <div className="bg-white shadow-sm border-b sticky top-0 z-10">
            <div className="max-w-7xl mx-auto px-4 py-4 flex justify-between items-center">
              <div className="flex items-center gap-4">
                <button onClick={() => setCurrentView("landing")} className="text-gray-600 hover:text-gray-800">← Back to Home</button>
                <h1 className="text-2xl font-bold text-gray-800">Management Panel</h1>
              </div>
              <div className="flex items-center gap-4">
                <button onClick={handleSave} className="bg-green-600 text-white px-4 py-2 rounded-lg flex items-center gap-2 hover:bg-green-700"> Save Menu </button>
                <a href="#/menu" target="_blank" rel="noopener noreferrer" className="bg-blue-600 text-white px-4 py-2 rounded-lg flex items-center gap-2 hover:bg-blue-700">
                  <Eye className="w-4 h-4" /> Preview Menu
                </a>
              </div>
            </div>
          </div>
          <div className="max-w-7xl mx-auto px-4 py-8 space-y-8">
            <div className="bg-white rounded-lg shadow-sm p-6"><div className="flex justify-between items-center mb-4"><h2 className="text-xl font-semibold">Restaurant Information</h2><button onClick={() => setEditingRestaurant(true)} className="bg-blue-600 text-white px-3 py-2 rounded-lg flex items-center gap-2 hover:bg-blue-700"><Edit3 className="w-4 h-4" /> Edit Info</button></div>{editingRestaurant ? (<div className="space-y-3"><input type="text" placeholder="Restaurant Name" value={selectedRestaurant?.name || ""} onChange={(e) => setSelectedRestaurant({...selectedRestaurant, name: e.target.value})} className="w-full px-3 py-2 border rounded-lg" /><textarea placeholder="Description" value={selectedRestaurant?.description || ""} onChange={(e) => setSelectedRestaurant({...selectedRestaurant, description: e.target.value})} className="w-full px-3 py-2 border rounded-lg" rows={3} /><div className="flex gap-2"><button onClick={() => updateRestaurant(selectedRestaurant)} className="bg-green-600 text-white px-4 py-2 rounded-lg hover:bg-green-700">Save</button><button onClick={() => setEditingRestaurant(false)} className="bg-gray-200 text-gray-700 px-4 py-2 rounded-lg hover:bg-gray-300">Cancel</button></div></div>) : (<div><p className="text-sm text-gray-600">Restaurant Name</p><p className="font-medium mb-3">{selectedRestaurant?.name || "Not set"}</p><p className="text-sm text-gray-600">Description</p><p className="font-medium">{selectedRestaurant?.description || "Not set"}</p></div>)}</div>
            <div className="bg-white rounded-lg shadow-sm p-6"><h2 className="text-xl font-semibold mb-4">Categories</h2><div className="flex gap-2 mb-4"><input value={newCategoryInput} onChange={(e) => setNewCategoryInput(e.target.value)} placeholder="Add new category" className="flex-1 px-3 py-2 border rounded-lg" onKeyPress={(e) => { if (e.key === 'Enter') { const trimmed = newCategoryInput.trim(); if (trimmed && !categories.includes(trimmed)) { setCategories(prev => [...prev, trimmed]); } setNewCategoryInput(""); } }} /><button onClick={() => { const trimmed = newCategoryInput.trim(); if (trimmed && !categories.includes(trimmed)) { setCategories(prev => [...prev, trimmed]); } setNewCategoryInput(""); }} className="bg-green-600 text-white px-4 py-2 rounded-lg hover:bg-green-700">Add</button></div>{categories.length > 0 ? (<div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-3">{categories.map(cat => (<div key={cat} className="flex justify-between items-center bg-gray-50 px-3 py-2 rounded-lg"><span className="font-medium">{cat}</span><button onClick={() => { setCategories(prev => prev.filter(c => c !== cat)); setMenuItems(prev => prev.map(item => item.category === cat ? {...item, category: ""} : item)); }} className="text-red-600 hover:text-red-800 text-sm">Remove</button></div>))}</div>) : (<p className="text-gray-500 text-center py-4">No categories added yet</p>)}</div>
            <div className="bg-white rounded-lg shadow-sm p-6"><div className="flex justify-between items-center mb-4"><h2 className="text-xl font-semibold">Menu Items ({menuItems.length})</h2><button onClick={() => setEditingItem({ name: "", category: categories[0] || "", price: "", description: "", imageUrl: "", popular: false, isVeg: true, chefSpecial: false })} className="bg-green-600 text-white px-4 py-2 rounded-lg flex items-center gap-2 hover:bg-green-700"><Plus className="w-4 h-4" /> Add Item</button></div>{menuItems.length > 0 ? (<div className="space-y-3">{menuItems.map(item => (<div key={item.id} className="border rounded-lg p-4 hover:bg-gray-50"><div className="flex items-center justify-between"><div className="flex items-center gap-3">{item.imageUrl ? (<img src={item.imageUrl} alt={item.name} className="w-12 h-12 rounded-lg object-cover cursor-pointer" onClick={() => setViewingImage(item.imageUrl)}/>) : (<div className="w-12 h-12 rounded-lg bg-gray-100 flex items-center justify-center">🍽️</div>)}<div><div className="font-medium flex items-center gap-2">{item.name}{item.popular && <span className="text-xs bg-orange-100 text-orange-800 px-2 py-1 rounded">Popular</span>}{item.chefSpecial && <span className="text-xs bg-purple-100 text-purple-800 px-2 py-1 rounded">Chef's Special</span>}{item.isVeg ? <span className="text-green-600">🟢</span> : <span className="text-red-600">🔴</span>}</div><div className="text-sm text-gray-500">{item.description}</div><div className="text-sm text-gray-500">Category: {item.category || "Uncategorized"} | Price: ₹{item.price}</div></div></div><div className="flex gap-2"><button onClick={() => setEditingItem(item)} className="text-blue-600 hover:text-blue-800 p-1"><Edit3 className="w-4 h-4" /></button><button onClick={() => deleteMenuItem(item.id)} className="text-red-600 hover:text-red-800 p-1"><Trash2 className="w-4 h-4" /></button></div></div></div>))}</div>) : (<div className="text-center py-8 text-gray-500"><Menu className="w-12 h-12 mx-auto text-gray-300 mb-4" /><p className="text-lg font-medium mb-2">No menu items yet</p><p className="text-sm">Add your first menu item to get started</p></div>)}</div>
            <div className="bg-white rounded-lg shadow-sm p-6"><div className="text-center"><div className="flex items-center justify-center gap-2 mb-4"><QrCode className="w-6 h-6" /><h2 className="text-xl font-semibold">Customer QR Code</h2></div><p className="text-gray-600 mb-6">Generate a QR code for customers to scan and instantly access your digital menu. No app downloads required!</p><div className="flex flex-col items-center gap-6"><div className="bg-white p-4 rounded-lg border-2 border-dashed border-gray-300">{isReadyForQR ? (<QRCodeComponent value={customerMenuUrl} size={220} />) : (<div className="w-55 h-55 bg-gray-100 rounded-lg flex items-center justify-center p-8"><div className="text-center text-gray-500"><QrCode className="w-12 h-12 mx-auto mb-2" /><p className="text-sm">QR Code will appear here</p></div></div>)}</div>{!isReadyForQR ? (<div className="bg-yellow-50 border border-yellow-200 rounded-lg p-4 max-w-md"><h3 className="font-medium text-yellow-800 mb-2">Setup Required</h3><p className="text-sm text-yellow-700 mb-2">Complete these steps to generate your QR code:</p><ul className="text-sm text-yellow-700 space-y-1">{!selectedRestaurant?.name?.trim() && <li>• Set restaurant name</li>}{categories.length === 0 && <li>• Add at least one category</li>}{menuItems.length === 0 && <li>• Add at least one menu item</li>}</ul></div>) : (<div className="text-center"><button onClick={() => { const qrCodeUrl = `https://api.qrserver.com/v1/create-qr-code/?size=220x220&data=${encodeURIComponent(customerMenuUrl)}`; downloadQRCode(selectedRestaurant?.name || "menu", qrCodeUrl); }} className="bg-green-600 text-white px-6 py-3 rounded-lg flex items-center gap-2 hover:bg-green-700 mx-auto"><Download className="w-5 h-5" /> Download QR Code</button><p className="text-xs text-gray-500 mt-3 max-w-md">Print this QR code and place it on tables. Customers can scan to view your menu instantly.</p></div>)}</div></div></div>
          </div>
          {editingItem && (<MenuItemForm item={editingItem} onSave={editingItem.id ? updateMenuItem : addMenuItem} onCancel={() => setEditingItem(null)} categories={categories} />)}
          {viewingImage && (<div className="fixed inset-0 bg-black bg-opacity-75 flex items-center justify-center z-50 p-4"><div className="relative max-w-4xl max-h-[90vh] w-full"><button onClick={() => setViewingImage(null)} className="absolute top-4 right-4 text-white hover:text-gray-300 z-10"><X className="w-8 h-8" /></button><img src={viewingImage} alt="Full size view" className="w-full h-full object-contain rounded-lg"/></div></div>)}
        </div>
      )}

      {currentView === "menu" && (
        <div className="min-h-screen bg-gray-50">
          <div className="bg-white shadow-sm sticky top-0 z-10">
            <div className="max-w-2xl mx-auto px-4 py-4 flex items-center justify-between">
              <button onClick={() => setCurrentView("landing")} className="text-gray-600 hover:text-gray-800">← Back to Home</button>
              <div className="text-center flex-1">
                <h1 className="text-xl font-bold text-gray-800">{selectedRestaurant?.name || "Restaurant Menu"}</h1>
                {selectedRestaurant?.description && (<p className="text-sm text-gray-600">{selectedRestaurant.description}</p>)}
              </div>
              <div style={{ width: 96 }} />
            </div>
          </div>
          <div className="max-w-2xl mx-auto px-4 py-6">
            {menuItems.length > 0 ? (
              menuItems.map(item => (
                <div key={item.id} className="p-4 mb-2 bg-white rounded shadow-sm">
                  <h3 className="font-semibold">{item.name}</h3>
                  <p className="text-sm text-gray-600">{item.description}</p>
                  <p className="font-bold">₹{item.price}</p>
                </div>
              ))
            ) : (
              <p className="text-center text-gray-500">No menu items to display.</p>
            )}
          </div>
        </div>
      )}
    </div>
  );
};
export default ScanToSeeMenuApp;